import React, { useEffect, useState } from "react";
import axios from "axios";
import styles from "./detail.module.css";
import Select from "react-select";
import { useLocation } from 'react-router-dom';
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, Legend } from 'recharts';

const data1 = [
    { name: 'Benchmark', value: 21945 },
    { name: 'ClockSpeed', value: 3.7 },
    { name: 'TurboSpeed', value: 4.6 },
    { name: 'Cores', value: 6 },
    { name: 'Price', value: 187956 },
];
const data2 = [
    { name: 'Benchmark', value: 11945 },
    { name: 'ClockSpeed', value: 2.7 },
    { name: 'TurboSpeed', value: 3.6 },
    { name: 'Cores', value: 4 },
    { name: 'Price', value: 137956 },
];
function CpuCompare() {
    const location = useLocation();
    const searchParams = new URLSearchParams(location.search);
    const id = searchParams.get('id');
    //  cpu
    const [selectedCpu, setSelectedCpu] = useState("");
    const [cpuOption, setCpuOption] = useState([]); // cpu   
    //  cpu
    const [cpuInfo, setCpuInfo] = useState([]);

    useEffect(() => {
        axios.get('/category/cpu1')
            .then(response => {
                const cpus = response.data.map(cpus => ({
                    value: cpus.cpu_name,
                    label: cpus.cpu_name
                }));
                setCpuOption(cpus);
            })
            .catch(error => {
                console.log(error);
            });
    }, []);






    const handleSubmit = (e) => {
        e.preventDefault();
    };

    function handleCpuChange(selectedCpu) {
        setSelectedCpu(selectedCpu);
    }

    useEffect(() => {
        axios.post('/cpuid', { id })
            .then(response => {
                setCpuInfo(response.data);
            })
            .catch(error => {
                console.log(error);
            });
    }, []);



    return(
        <>
            <div className={styles.container1}>
                <div className={styles.item}> </div>
                <form onSubmit={handleSubmit} className={styles.item} >
                    <label> Cpu  : </label>
                    <Select
                        value={selectedCpu}
                        onChange={handleCpuChange}
                        options={cpuOption}
                        placeholder="Choose an option"
                        isSearchable={true}
                        className={styles.selectTag}
                    />
                    <label htmlFor="cpuSelect">Selected Cpu : &nbsp;</label>
                    <input name = "cpuSelect" className={styles.selectTagShow} value={selectedCpu ? selectedCpu.label : ''} />
                    <br/>
                </form>

            </div>
            <div className={styles.container2}>
                <div className={styles.item}>   
                    <BarChart width={500} height={300} data={data1} >
                        <CartesianGrid strokeDasharray="3 3" />
                        <XAxis dataKey="name" />
                        <YAxis scale="log" />
                        <Tooltip />
                        <Legend />
                        <Bar dataKey="value" fill="#8884d8" />
                    </BarChart>
                </div>
                <div className={styles.item}>   
                    <BarChart width={500} height={300} data={data2}>
                        <CartesianGrid strokeDasharray="3 3" />
                        <XAxis dataKey="name" />
                        <YAxis
                            tickFormatter={(tick) =>
                                30000 > tick > 1000 ? `${(tick / 1000)}` : tick
                            }
                            tickFormatter2={(tick) =>
                                tick > 30000 ? `${(tick / 30000)}` : tick
                            }
                        />
                        <Tooltip />
                        <Legend />
                        <Bar dataKey="value" fill="#8884d8" />
                    </BarChart>
                </div>
            </div>
        </>
    );
}


export default CpuCompare;